.sidebar {
    flex: 1 1;
    height: calc(100vh - 50px);
    background-color: rgb(253, 253, 255);
    position: -webkit-sticky;
    position: sticky;
    top: 50px;
}

.sidebarWrapper {
    padding: 20px;
    color: #555;
}

.sidebarMenu {
    margin-bottom: 10px;
}

.sidebarTitle {
    font-size: 20px;
    color: rgb(187, 186, 186);
}

.sidebarList {
    list-style: none;
    padding: 5px;
}

.sidebarListItem {
    padding: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 10px;
}

.sidebarListItem.active, 
.sidebarListItem:hover {
    background-color: #fad6a5;
}

.sidebarIcon {
    margin-right: 5px;
    font-size: 20px;

}
/* Main topbar css */
.topbar {
    width: 100%;
    height: 70px;
    background-color: rgb(253, 253, 255); /* Maybe change to white? */
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
}

.middle h2 {
    color: lightgray;
}

.middle h2:hover {
    color: #fad6a5;
}

.topbarWrapper {
    height: 100%;
    padding: 0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between; /* Maybe 'space-evenly/around' is more appropriate for allplan website */
}

.logo {
    cursor: pointer;
    /* Remove the following code if you change 'space-between' above: */
    position: relative;
    left: 80px;
}

.topLeft {
    margin-top: 20px;
    
}

.topRight {
    display: flex;
    align-items: center;
}

.topbarIconContainer {
    position: relative;
    cursor: pointer;
    margin-right: 20px;
    margin-top: 12px;
}

.topIconBadge {
    width: 15px;
    height: 15px; 
    position: absolute;
    top: -5px;
    right: 0px;
    background-color: lightsalmon;
    color: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
}

.topAvatar {
    width: 37px;
    height: 37px;
    border-radius: 50%;
    cursor: pointer;
    margin-top: 12px;
}


.chart {
    margin: 20px;
    padding: 20px; 
    box-shadow: 5px 5px 15px -5px #000000;
}


.chart {
    margin: 20px;
    padding: 20px; 
    box-shadow: 5px 5px 21px -14px #000000;
}

.chartTitle {
    margin-bottom: 20px;
}

.inline-block-child {
    display: inline-block;
    margin-left: 10px;
}

.inline-block-child button {
    display: inline-block;
    padding: 5px 15px;
    font-size: 15px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #fff;
    background-color: #fad6a5;
    border: none;
    border-radius: 15px;
    box-shadow: 0 2px #999;
}

.inline-block-child button:hover {
    background-color: orange;
}

.inline-block-child button:active {
    background-color: orange;
    box-shadow: 0 3px #666;
    -webkit-transform: translateY(4px);
            transform: translateY(4px);
}

.inline-block-childafter button {
