.featured {
    margin-top: 25px;
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.featuredItem {
    flex: 1 1;
    margin: 0px 20px;
    padding: 30px;
    border-radius: 10px; 
    cursor: pointer; 
    box-shadow: 5px 5px 21px -14px #000000;
}

.featuredTitle {
    font-size: 20px;
}

.featuredGraphContainer {
    margin: 10px 0px;
    display: flex;
    align-items: center;
}

.featuredGraph {
    font-size: 30px;
    font-weight: 600;
}

.chart {
    margin-top: 20px;
}

.featuredGraphRate {
    display: flex;
    align-items: center;
    margin-left: 20px;
}

.featuredSub {
    font-size: 15px;
    color: gray;
}
.home {
    flex: 4 1;
}

.homeWidgets {
    display: flex;
    margin: 20px;
}
.widgetSm {
    flex: 1 1; 
    box-shadow: 5px 5px 21px -14px #000000;
    padding: 20px;
    margin-right: 20px;
}

.widgetSmTitle {
    font-size: 22px;
    font-weight: 600;
}

.widgetSmImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.widgetSmList {
    margin: 0;
    padding: 0;
    list-style: none;
}

.widgetSmListItem {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 0px;
}

.widgetSmUser {
    display: flex;
    flex-direction: column;
}

.widgetSmUsername {
    font-weight: 600;
}

.widgetSmUserTitle {
    font-weight: 300;
    color: rgb(128, 128, 128);
}

.widgetSmButton {
    display: flex;
    align-items: center;
    border: none;
    border-radius: 10px;
    padding: 7px 10px;
    background-color: #eeeef7;
    color: #555;
    cursor: pointer;
}

.widgetSmIcon {
    font-size: 16px;
    margin-right: 5px;
}
.widgetLg {
    flex: 2 1; 
    box-shadow: 5px 5px 21px -14px #000000;
    padding: 20px;
}

.widgetLgTitle {
    font-size: 22px;
    font-weight: 600;
}

.widgetLgTable {
    width: 100%;
    border-spacing: 20px;
}

.widgetLgTh {
    text-align: left;
}

.widgetLgUser {
    display: flex;
    align-items: center;
    font-weight: 600;
}

.widgetLgImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.widgetLgDate {
    font-weight: 300;
    color: rgb(128, 128, 128);
}

.widgetLgAmount {
    font-weight: 300;
    color: rgb(128, 128, 128);
}

.widgetLgButton {
    padding: 5px 7px;
    border: none;
    border-radius: 10px;
}

.widgetLgButton.Approved {
    background-color: #e5faf2;
    color: #3bb077;
}

.widgetLgButton.Declined {
    background-color: #fff0f1;
    color: #d95087; 
}

.widgetLgButton.Pending {
    background-color: #ebf1fe;
    color: #2a7ade;
}
